.container{
    z-index: 8;
    grid-column: 2 / span 1;
    grid-row: 2 / -1;
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-flow: column nowrap;
    padding: 0 1em;
    overflow-y: scroll;
    scroll-behavior: smooth;
}
.container::-webkit-scrollbar{
    display: none;
}