    --light-black: #4f545c;
    --lighter-black: #72767d;

    --darker-violet: #322a54;
    --dark-violet : #36303f;
    --violet : #5d5381;
    --light-violet : #754bce;
    
    --profile-card-aspect-ratio: 0.55238095238095238095238095238095;
    --profile-card-height: 60vh;
}

.Layout_mainContainer__25ZmF{
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(328deg, rgba(0,0,0,1) 0%, #1a1c20 70% , rgba(55,58,64,1) 100%);
    display: grid;
    grid-template-columns: 
        minmax(200px, calc( 60vh * 0.55238095238095238095238095238095))
        1fr 
        10vw;
    grid-template-columns: 
        minmax(200px, calc( var(--profile-card-height) * var(--profile-card-aspect-ratio)))
        1fr /*content*/
        10vw; /*navigation reader*/
    grid-template-rows: 
        7vh  /*header height*/
        1fr;
    grid-gap: 3rem 3rem;
    gap: 3rem 3rem;
    padding: 0 6rem;
}

