.ProfileCard_iconContainer__1aGrG::after{
    content: attr(data-url);
    font-size: 18px;
    padding: .4em;
    white-space: nowrap;
    border-radius: 8px;
    background-color: var(--darker-violet);
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    color: white;
    visibility: hidden;
    transition: all .2s;
    
}
.ProfileCard_iconContainer__1aGrG::before{
    content:'';
    position: absolute;
    top: 0;
    left: 50%;
    background-color: var(--darker-violet);
    width: 10px;
    height: 10px;
    transform: rotate(45deg) translateX(-50%) scale(0);
    color: white;
    visibility: hidden;
    transition: all .2s;
}

.ProfileCard_iconContainer__1aGrG:hover{
    transform: translateY(-5px);
    box-shadow: 0px 10px 4px -4px black ;
    background-color: var(--darker-violet);
    color: white;
    border: 2px solid black;
}
.ProfileCard_iconContainer__1aGrG:hover>.ProfileCard_icon__5n5gB{
    transform: scale(0);
}

.ProfileCard_iconContainer__1aGrG:hover::after{
    visibility: visible;
    top: -50%;
    transform: translate(-50%, -50%) scale(1);
}
.ProfileCard_iconContainer__1aGrG:hover::before
{
    visibility: visible;
    top: -.4em;
    transform: rotate(45deg) translateX(-50%) scale(1);
}
